.mid{font-family:"Poppins", sans-serif;margin-bottom:50px}.mid .info-header-holder{width:50%;display:flex;margin:auto;justify-content:center;text-align:center;margin-top:-15px}.mid h1{color:#06f;font-size:275%;font-weight:800}.mid .card-container .col{margin:30px 0}.mid .card-container .col .card{border-radius:1rem !important;padding-bottom:10px;box-shadow:0 10px 50px rgba(0,0,0,0.2);border:none}.mid .card-container .col .card .card-body{padding-left:10%;padding-top:5%;height:520px}.mid .card-container .col .card .format{margin-top:0px;margin-left:-15px;margin-right:5px;background-color:#06f;width:7.5px;height:65px;float:left;border-radius:0 15px 0 15px}.mid .card-container .col .card .card-title{font-size:190%;font-weight:700;margin-right:-5%;width:220px;color:#06f;margin-bottom:10px;line-height:100%}.mid .card-container .col .card .card-img-top{padding-top:15px;padding:5% 0;margin-left:-2.3em;width:100%}.mid .card-container .col .card .globeIcon{margin-left:45px;width:65%;margin-top:35px;margin-bottom:68.3px}.mid .card-container .col .card .marketIcon{margin-left:-1em}.mid .card-container .col .card .governanceIcon{margin-left:-15px}.mid .card-container .col .card .card-text{margin-top:-50px;font-size:150%;font-weight:600;color:#919191}.mid .learnContainer{width:100%;margin:auto;text-align:center;margin-top:-10px;font-size:30px;font-weight:600;color:#06f;margin-bottom:-20px}.mid .learnContainer .btn{margin-top:-15px;font-size:35px;font-weight:700;background-color:#06f;height:65px;width:250px;border-radius:15px;border-style:none;box-shadow:0 15px 25px rgba(0,0,0,0.2)}

.breaker{font-family:"Poppins", sans-serif;width:100%;background:#06f}.breaker .row{margin:0}.breaker h1{color:white;font-weight:800;font-size:55px;text-align:center;margin-top:30px}.breaker .guide .container{margin-top:10px;text-align:center}.breaker .guide .container .section{vertical-align:middle;display:inline-block;align-items:center;margin:0 30px}.breaker .guide .container .sec{color:rgba(255,255,255,0.7);font-weight:700;font-size:17.5px;width:130px}.breaker .guide .container .sec .node{margin:auto;height:80px;width:80px;background-color:rgba(255,255,255,0.7);border-radius:50%;margin-bottom:15px;font-size:25px;color:white;font-size:20px;line-height:25px}.breaker .guide .container .sec .node p{padding-top:15px}.breaker .guide .container .prim{color:white;font-weight:800;font-size:25px}.breaker .guide .container .prim .node{margin:auto;height:100px;width:100px;background-color:white;border-radius:50%;margin-bottom:15px;color:#06f;font-size:30px;line-height:35px}.breaker .guide .container .prim .node p{padding-top:12.5px}.breaker .roadmap{background-color:rgba(255,255,255,0.1)}.breaker .roadmap div{width:100%;margin:auto;text-align:center}.breaker .roadmap .btn{margin-top:60px;margin-bottom:100px;font-size:35px;font-weight:700;background-color:#06f;height:65px;width:250px;border-radius:15px;border-style:none;box-shadow:0 15px 25px rgba(0,0,0,0.2)}.breaker .roadmap .btn-secondary:hover{box-shadow:0 25px 35px rgba(0,0,0,0.4);transition-duration:0.15s}.breaker .community div{margin:auto;margin-top:40px;text-align:center}.breaker .community .media{height:80px;width:80px;margin:0 18px;background-color:white;border-radius:50%;display:inline-block;margin-bottom:50px;position:relative;text-align:center;justify-content:center}.breaker .community .media p{display:flex;justify-content:center;margin-top:25px;color:white;font-weight:600;font-size:25px}.breaker .community .media img{margin-top:15px;height:60%;width:60%}.breaker .community .email img{padding:5px 0}

body{background-color:#efefef}.notif{padding:0 30px;padding-top:30px;margin-bottom:-10px;font-family:"Poppins", sans-serif;text-align:center;font-weight:700;font-size:15px;color:#e2e2e2 !important}.crowdsale-container{font-family:"Poppins", sans-serif;text-align:center}.crowdsale-container .primary{color:#06f}.crowdsale-container .subtext{margin:0;color:#919191;font-weight:600}.crowdsale-container h1.primary{font-weight:800;font-size:40px}.crowdsale-container p.primary{font-weight:800;font-size:20px}.crowdsale-container p.subtext{font-size:25px}.crowdsale-container .bar{justify-content:center;flex-direction:row;align-items:center}.crowdsale-container .bar img{margin-top:25px;margin-bottom:40px;height:600px}.crowdsale-container .sub-bar{margin:0 20%;float:center;text-align:left}.crowdsale-container .sub-bar img{height:70px;float:left}.crowdsale-container .sub-bar h1{font-size:30px;margin-bottom:0;margin-left:2.55em}.crowdsale-container .sub-bar p{padding-left:80px}.crowdsale-container .sale-breaker{background-color:#06f}.crowdsale-container .sale-breaker h1{color:white;margin-top:30px;font-size:55px}.crowdsale-container .sale-breaker p{color:white;font-size:35px}.crowdsale-container .sale-breaker .container .col{margin:0}.crowdsale-container .sale-breaker .date{margin-top:-10px;font-size:35px;font-weight:800}.crowdsale-container .sale-breaker .rate{margin-top:15px}.crowdsale-container .sale-breaker .rates p{line-height:35px;font-size:25px;font-weight:700;margin:0}.crowdsale-container .sale-breaker .alert{margin:0}.crowdsale-container .sale-breaker .minor{font-size:20px !important;margin-top:-30px}.crowdsale-container .sale-breaker .subtitle{font-size:25px;font-weight:700;color:#4aaeff}.crowdsale-container .sale-breaker .dec{margin-top:25px;line-height:30px}.crowdsale-container .sale-breaker .date{margin-top:5px;font-size:25px;margin-bottom:10px}.crowdsale-container .sale-breaker .presale-container{line-height:40px}.crowdsale-container .sale-breaker .publicsale-contai